<script>

import Paper from './Paper'

export default {
    name: 'dt-papers',
    components: {
        'dt-paper': Paper,
    },
    props: {
        person: {
            type: Object,
            default: _ => undefined,
        },
        years: {
            type: Array,
            default: _ => [2018, 2017],
        },
    },
    data() {
        return {
            filter: {
                onlySupported: {
                    v: true,
                    name: 'Only supported by "Megagrant"',
                    name_rus: 'Только поддержанные "МегаГрантом"',
                    check: (p, v) => !v || p.support == 'Megagrant',
                },
                withConfProcd: {
                    v: true,
                    name: 'Show conference papers',
                    name_rus: 'Показывать публикации конференций',
                    check: (p, v) => v || p.kind != 'conference',
                },
                years_: {
                    v: [2017],
                    enum: [2017, 2018],
                    name: '',
                    name_rus: '',
                    check: (p, v) => v.some(y => y==p.year),
                },
                withPreprints: {
                    v: true,
                    name: 'Show preprints',
                    name_rus: 'Показывать препринты',
                    check: (p, v) => v || ['ArXiv preprint', 'bioRxiv'].every(n => p.journal && n!=p.journal.name),
                },
            },
        }
    },
    methods: {
        filter_(paper) {
            if(this.person)
                return ~paper.author.indexOf(this.person.surname) && this.filter.years_.check(paper, this.years)
            for(let [name, f] of Object.entries(this.filter))
                if(!f.check(paper, f.v))
                    return false
            return true
        },
    },
}
</script>

<template lang="pug">

div.dt-papers
    dkbr-progress.dt-papers__progress(
        v-if="!papersTbl.isParsed"
        :isThInv="papersTbl.isLoaded ? false : true"
        :isInf="true")
    div.dt-papers__header(
        v-if=" papersTbl.isParsed && !person")
        p.dt-title.dt-papers__header-title
            span(v-if="isENG") Publications of the team members.
            span(v-if="isRUS") Научные публикации сотрудников Лаборатории.
        div.dt-papers__header-filters
            dkbr-input.dt-papers__header-filters-filter(
                v-for="[n, f] of Object.entries(filter)"
                :key="n"
                th="th1"
                :isThInv="true"
                :value="f.v"
                :type="n != 'years_' ? 'boolean' : 'array'"
                :enum="f.enum"
                :name="f['name'+ (isENG ? '' : '_rus')]"
                :isCheckBox="n != 'onlySupported' && n != 'years_'"
                :isEditable="n != 'years_'"
                @edited="value => {f.v = value}")
    transition-group.dt-papers__content(
        tag="div"
        name="dt-papers__content")
        dt-paper.dt-papers__paper(
            v-if="filter_(paper)"
            v-for="paper of papersTbl.rows"
            :key="paper.id"
            :paper="paper"
            :person="person")
</template>

<style lang="sass">

@import '~@dt/sass/root.sass'

.dt-papers
    &__progress
        position: absolute
        left: 0
        top: 0
        padding: 0
        margin: 0
    &__header
        +flex(row, wrap, space-between, center)
        +margin-b(20px)
        &-title
            flex: 1 0 300px
            +padding-r(20px)
        &-filters
            flex: 1 1 auto
            +padding-b(5px)
            +flex(row, wrap, space-between, center)
            +border(light-bottom)
            +font(base-small)
            &-filter
                flex: 1 1 40%
                +margin(0, 10px)
    &__content
        +flex(column)
        &-enter
            opacity: 0
            transform: translate(0, 100%)
        &-leave-to
            opacity: 0
            transform: translate(-600px, 200px)
            z-index: 0
        &-leave-active
            position: absolute
    &__paper
        transition: all 1s
        margin-top: 10px
</style>



