<script>

import PaperMixin from './_PaperMixin'
import PaperInfo from './PaperInfo'

export default {
    name: 'dt-paper',
    mixins: [PaperMixin],
    components: {
        'dt-paper-info': PaperInfo,
    },
    props: {
        person: {
            type: Object,
            default: _ => undefined,
        },
    },
    data() {
        return {
            isShowInfo: false,
            isCopied: false,
            errCopy: '',
        }
    },
    computed: {

    },
    created() {
        this.date
    },
    methods: {
        onCopy(e) {
            this.isCopied = true
        },
        onCopyError(e) {
            this.isCopied = true
        },
    },
}
</script>

<template lang="pug">

dkbr-frame.dt-paper(
    th="th7"
    :isThInv="true"
    :elevation="!person ? 20 : 0")
    div.dt-paper__header(
        v-if="!person")
        dkbr-icon(
            name="local_library"
            :text="paper.year + (support ? ' | ' + support : '')")
    p.dt-paper__title
        span {{ paper.title }}
        span(
            v-if="journalName") &nbsp;// {{ journalName }}
    div.dt-paper__box
        p.dt-paper__author
            span(
                v-for="(author, i) of authors"
                :key="i"
                :class=`{'dt-paper__author_person': author.isInTeam}`) {{ author.name }} {{ author.isETC ? 'and others' : '' }}{{ author.surname }}{{ author.isLast ? '.' : ', '}}
        div.dt-paper__actions
            dkbr-button.dt-paper__action(
                v-if="IsEnabledInfo"
                th="th3"
                :isRaised="true"
                :isDense="true"
                icon="info"
                @click.native="isShowInfo = !isShowInfo")
            dkbr-button.dt-paper__action(
                th="th3"
                :href="paper.url"
                target="_blank"
                :isRaised="true"
                :isDense="true"
                :desc="isENG ? 'Publication on the publisher site' : 'Публикация на сайте издателя'"
                tltpDir="left"
                icon="public")
    dt-paper-info.dt-paper__info(
        v-if="isShowInfo && IsEnabledInfo"
        :paper="paper")
</template>

<style lang="sass">

@import '~@dt/sass/root.sass'

.dt-paper
    +margin(10px)
    +padding(10px 20px 5px)
    &__box
        +flex(row, nowrap, space-between, center)
    &__header
        +color-font(dark3)
        +font(base-bold-small)
    &__title
        +margin(0)
        +padding(2px 0 5px)
        +color-font(dark3)
        +font(base-bold-big)
    &__author
        flex: 1 0 200px
        +margin(3px 10px 20px)
        +padding(0 20px)
        &_person
            +color-font(light3)
            +font(base-bold)
    &__actions
        flex: 0 0 50px
        +flex(row, nowrap, flex-end)
        +padding-r(10px)
    &__action
        position: relative
        display: block
        +margin(0 10px)
        +padding(0)

</style>



